<template>
    <div class="bread_crumb_box">
        <div  class="bread_crumb_box_left">
            <span class="bread_crumb_box_left_parent" @click="goBack">{{name}}</span>
            <span v-if="type === 2">>> <span class="bread_crumb_box_left_child">{{childName}}</span></span>
        </div>
        <div v-if="type === 1" class="bread_crumb_box_back" @click="goHome">返回首页>></div>
    </div>
</template>

<script>

    export default {
        data(){
            return {
                name:'',
                childName:'',
                type:'',
            }
        },
        created(){
            this.name = this.$route.meta.parent ? this.$route.meta.parentName : this.$route.meta.nav;
            this.childName = this.$route.meta.nav;
            this.type = this.$route.meta.type;
        },
        mounted() {
        },
        methods:{
            goBack(){
                this.$router.push({path:this.$route.meta.parent})
            },
            goHome(){
                this.$router.push({path:'/'})
            }
        }
    }
</script>
<style lang="scss">
    $prefix: bread_crumb_box;

    .#{$prefix} {
        display: flex;
        justify-content: space-between;
        padding: 40px 0 5px 0;
        border-bottom: 5px solid rgba(36, 130, 96, 0.99);
        &_left{
            &_parent{
                cursor: pointer;
                margin-right: 5px;
            }
        }
        &_back{
            font-size: 12px;
            cursor: pointer;
        }
    }
</style>
